.statistics {
    text-align: center;

    width: 240px;
    margin: 20px auto;

    overflow: hidden;
    border: 1px solid #3f3f3f;
    border-radius: 5px;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

.title {
    text-transform: uppercase;
    line-height: 1.7;
    font-size: 24px;
    font-weight: 500;
}

.statisticsList {
    display: flex;
    justify-content: center;
}